<template>
    <div>
    <div>
        <el-button icon="el-icon-plus" @click="showAddSalary">添加工资账套</el-button>
        <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogVisible"
                width="50%">
            <div style="display: flex;justify-content: space-around;align-items: center">
                <el-steps direction="vertical" :active="active">
                    <el-step :title="ItemName" v-for="(ItemName,index) in salaryItemName" :key="index"></el-step>
                </el-steps>
                <el-input
                        v-model="salary[title]"
                        :placeholder="'请输入'+salaryItemName[index]+'...'"
                        v-for="(value,title,index) in salary"
                        :key="index"
                        v-show="active==index"
                        style="width: 200px;"
                >
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="preStep">{{active==0?'取消':'上一步'}}</el-button>
                <el-button type="primary" @click="nextStep">{{active==9?'完成':'下一步'}}</el-button>
            </span>
        </el-dialog>
        <el-button icon="el-icon-refresh"></el-button>
    </div>
    <div>
        <el-table v-bind:data="salaries" border stripe>
            <el-table-column type="selection" width="30"></el-table-column>
            <el-table-column width="120" prop="name" label="账套名称"></el-table-column>
            <el-table-column width="70" prop="basicsalary" label="基本工资"></el-table-column>
            <el-table-column width="70" prop="trafficsalary" label="交通补助"></el-table-column>
            <el-table-column width="70" prop="lunchsalary" label="午餐补助"></el-table-column>
            <el-table-column width="70" prop="bonus" label="奖金"></el-table-column>
            <el-table-column width="70" prop="createdate" label="启用时间"></el-table-column>
            <el-table-column align="center" label="医疗保险">
                <el-table-column width="70" prop="medicalper" label="比率"></el-table-column>
                <el-table-column width="70" prop="medicalbase" label="基数"></el-table-column>
            </el-table-column>
            <el-table-column align="center" label="养老金">
                <el-table-column width="70" prop="pensionper" label="比率"></el-table-column>
                <el-table-column width="70" prop="pensionbase" label="基数"></el-table-column>
            </el-table-column>
            <el-table-column align="center" label="公积金">
                <el-table-column width="70" prop="accumulationfundper" label="比率"></el-table-column>
                <el-table-column width="70" prop="accumulationfundbase" label="基数"></el-table-column>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="showEditSalaryView(scope.row)">编辑</el-button>
                    <el-button type="danger" @click="deleteSalary(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
    </div>
</template>

<script>
    export default {
        name: "SalSob",
        data(){
            return {
                salaries:[],
                dialogVisible:false,
                dialogTitle:'添加工资账套',
                active:0,
                salaryItemName:[
                    '基本工资',
                    '交通补助',
                    '午餐补助',
                    '奖金',
                    '养老金比率',
                    '养老金基数',
                    '医疗保险比率',
                    '医疗保险基数',
                    '公积金比率',
                    '公积金基数',
                    '账套名称'
                ],
                salary:{
                    basicsalary:0,
                    trafficsalary:0,
                    lunchsalary:0,
                    bonus:0,
                    pensionper:0,
                    pensionbase:0,
                    medicalper:0,
                    medicalbase:0,
                    accumulationfundper:0,
                    accumulationfundbase:0,
                    name:''
                }
            }
        },
        mounted(){
          this.initSalaries();
        },
        methods:{
            showEditSalaryView(data){
              this.dialogTitle='修改工资账套';
              this.salary.basicsalary=data.basicsalary;
              this.salary.lunchsalary=data.lunchsalary;
              this.salary.trafficsalary=data.trafficsalary;
              this.salary.pensionbase=data.pensionbase;
              this.salary.pensionper=data.pensionper;
              this.salary.accumulationfundbase=data.accumulationfundbase;
              this.salary.accumulationfundper=data.accumulationfundper;
              this.salary.bonus=data.bonus;
              this.salary.name=data.name;
              this.salary.medicalbase=data.medicalbase;
              this.salary.medicalper=data.medicalper;
              this.salary.id=data.id;
                this.dialogVisible=true;
            },
            deleteSalary(data){
                this.$confirm('此操作将会删除，请确认是否继续！','提示',{
                    cancelButtonText:'取消',
                    confirmButtonText:'确定'
                }).then(()=>{
                    this.deleteRequest("/salary/sob/"+data.id).then(resp=>{
                        if(resp){
                            this.initSalaries()
                        }
                    })
                }).catch(()=>{
                    this.$message.info('取消删除');
                })
            },
            preStep(){
                if(this.active==0){
                    alert("我来了");
                    return;
                }
              this.active--;
            },
            nextStep(){
                if(this.active==10){
                    if(this.salary.id){
                        this.putRequest("/salary/sob/",this.salary).then(resp=>{
                            if(resp){
                                this.initSalaries();
                                this.dialogVisible=false;
                            }
                        })
                    }else {
                        this.postRequest("/salary/sob/",this.salary).then(resp=>{
                            if(resp){
                                this.initSalaries();
                                this.dialogVisible=false;
                            }
                        })
                    }

                    return;
                }
                this.active++;
            },
            showAddSalary(){
                this.dialogTitle='添加工资账套';
                this.dialogVisible=true;

            },
            initSalaries(){
                this.getRequest("/salary/sob/").then(resp=>{
                    if(resp){
                        this.salaries =resp ;
                    }
                })
            }
        }
    }
</script>

